import { Component, OnInit, EventEmitter, Output, QueryList, ViewChildren } from '@angular/core';
import { DependencyTableHelisaService, ConfigTable } from './dependency-table-helisa.service';
import { ColumnConfig, RequestTableHelisa } from '../table-helisa/table-helisa.interface';
import { TableHelisaService } from '../table-helisa/table-helisa.service';
import { TableHelisaComponent } from '../table-helisa/table-helisa.component';

export interface EventDependency{
  index: number,
  data: any
}

@Component({
  selector: 'hel-dependency-table',
  templateUrl: './dependency-table-helisa.component.html',
  styleUrls: ['./dependency-table-helisa.component.sass'],
  providers: [ DependencyTableHelisaService ]
})
export class DependencyTableHelisaComponent implements OnInit {

  tables: Array<ConfigTable> = [];
  @ViewChildren('viewTables') viewTables: QueryList<TableHelisaComponent<any>>;
  

  @Output() selected: EventEmitter<EventDependency> = new EventEmitter<EventDependency>();
  @Output() nextPage: EventEmitter<EventDependency> = new EventEmitter<EventDependency>();
  @Output() total: EventEmitter<EventDependency> = new EventEmitter<EventDependency>();
  @Output() sort: EventEmitter<EventDependency> = new EventEmitter<EventDependency>();

  constructor(private dependencyTableHelisaService: DependencyTableHelisaService, private tableService: TableHelisaService<any>) { }

  ngOnInit() {
    this.getTables();
    this.dependencyTableHelisaService.emitNextPage.subscribe(
      event => {
        this.tableService.addPage(event.data, this.viewTables.toArray()[event.index]);
      }
    );
    
    this.dependencyTableHelisaService.emitTotal.subscribe(
      event => {
        this.tableService.setTotal(event.data, this.viewTables[event.index]);
      }
    );
  }

  /**
   * retorna el servicio que gestiona el componente.
   */
  getService() : DependencyTableHelisaService {
    return this.dependencyTableHelisaService;
  }

  /**
   * Obtiene un observable con las tablas dependientes desde el servicio.
   */
  getTables(){
    this.dependencyTableHelisaService.getTables()
      .subscribe(tables => {
        this.tables = tables;
      }
    );
  }

  /**
   * Evento que se dispara desde una tabla, emitiendo un nuevo evento con el inidice de la tabla que dispara el evento y el evento generado.
   * @param index indica el indice de la tabla seleccionada
   * @param data retorna la fila que fue seleccionada
   */
  onSelectedDependency(index: number, event: any){
    this.selected.emit({index:index, data:event});
  }

  /**
   * Evento que se dispara desde una tabla, emitiendo un nuevo evento con el inidice de la tabla que dispara el evento y el evento generado.
   * @param index indica el indice de la tabla que genera el evento
   * @param event evento generado desde la tabla
   */
  onNextPage(index: number, event: RequestTableHelisa) {
    this.nextPage.emit({index:index, data:event});
  }

  /**
   * Evento que se dispara desde una tabla, emitiendo un nuevo evento con el inidice de la tabla que dispara el evento y el evento generado.
   * @param index indica el indice de la tabla que genera el evento
   * @param event evento generado desde la tabla
   */
  onTotal(index: number, event: any){
    this.total.emit({index:index, data:event});
  }

  /**
   * Evento que se dispara desde una tabla, emitiendo un nuevo evento con el inidice de la tabla que dispara el evento y el evento generado.
   * @param index indica el indice de la tabla que genera el evento
   * @param event evento generado desde la tabla
   */
  onSort(index: number, event: any){
    this.sort.emit({index:index, data:event});
  }



}
