import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormControl } from '@angular/forms';
import { Observable } from 'rxjs';
import {map, startWith} from 'rxjs/operators';

export interface OptionAutocompleteHelisa<T> {
  value: T;
  displayText: string;
}

@Component({
  selector: 'hel-autocomplete',
  templateUrl: './autocomplete-helisa.component.html',
  styleUrls: ['./autocomplete-helisa.component.css']
})
export class AutocompleteHelisaComponent<T> implements OnInit {

  myControl = new FormControl();
  @Input() options = new Array<OptionAutocompleteHelisa<T>>();
  filteredOptions: Observable<OptionAutocompleteHelisa<T>[]>;
  selectedValue: OptionAutocompleteHelisa<T>;
  @Output() onSelectedValue: EventEmitter<T> = new EventEmitter<T>();

  ngOnInit() {
    this.filteredOptions = this.myControl.valueChanges.pipe(
      startWith(''),
      map(value => this._filter(value)) 
    );
  }

  private _filter(value): OptionAutocompleteHelisa<T>[] {
    if (value instanceof Object) {
      this.myControl.setValue(value.displayText); 
    }
    else {
      const filterValue = value.toLowerCase().split(' ');
      return this.options.filter(option => {
        let ws = true;
        filterValue.forEach(text => ws = ws && option.displayText.toLowerCase().indexOf(text) >= 0);
        return ws;
      }).splice(0, 5);
    }
  }

  public onSelected(event) { 
    this.selectedValue = event.option.value; 
    this.onSelectedValue.emit(this.selectedValue.value);
  }
}
